.cmp-Minicart__discountList__root {
    display: grid;
    grid-gap: 1rem;
    margin: 0 1.5rem;
    font-size: 14px;
    padding-bottom: 1.5rem;
}

.cmp-Minicart__discountList__item {
    display: grid;
    grid-template-columns: 75% auto;
}

.cmp-Minicart__discountList__price {
    text-align: right;
}
